<template>
<div @dblclick="$emit('toggle-reminder',task.id)" :class="[task.reminder? 'reminder':'','task']">
    <h3>{{task.text}}
       <i @click="onDelete(task.id)" class="fas fa-times"></i>
    </h3>
    <p>{{task.day}}</p>
</div>
   
</template>
<script >
export default { 
    name:'Task',
    props:{ 
        task:Object
    },
    methods:{ 
        onDelete(id){ 
            //console.log(id)
            this.$emit('delete-task', id);
        }
    }
}

</script>
<style>
    .fas{ 
        color: red;
    }
    .task{ 
        background: rgb(128, 125, 125);
        margin: 5px;
        padding: 10px 20px;
        cursor: pointer;
    }
    .task.reminder{ 
        border-left: 5px solid rgb(93, 214, 93);
    }
    .task h3{ 
        display:flex;
        align-items: center;
        justify-content: space-between;
    }
</style>